
import { mapState } from "vuex";
import { defineComponent } from "vue";
import {
  IonGrid,
  IonRow,
  IonCol,
  IonList,
  IonItem,
  IonLabel,
  IonPage,
  IonBackButton,
  IonToolbar,
  IonContent,
  IonTitle,
  IonHeader,
  IonButtons,
  IonListHeader,

} from "@ionic/vue";
import useAxios from "../composables/useAxios";
export default defineComponent({
  name: "BarPage",
  computed: {
    ...mapState("auth", { borovnicke: "borovnicke" }),
  },
  components: {
    IonGrid,
    IonRow,
    IonCol,
    IonList,
    IonItem,
    IonLabel,
    IonPage,
    IonBackButton,
    IonToolbar,
    IonContent,
    IonTitle,
    IonHeader,
    IonButtons,
    IonListHeader,
  },
  data() {
    return {
      bars: [] as any,
      show: false,
      dialog: false,
    };
  },
  setup() {
    let getBars = useAxios();
    //Dinamično za url - poglej @GrujicBard
    //Mogoče ni najbolj user friendly ampak dela trenutn
    var url = window.location.pathname;
    var barId = url.substring(url.lastIndexOf("/") + 1);
    return { getBars, barId };
  },
  async mounted() {
    await this.getBars.get(`bars/${this.barId}`);
    this.bars = this.getBars.result.value;
    this.show = true;
  },
  methods: {
    currentDateTime() {
      const current = new Date();
      const date = current.getDay();
      const dateTime = date;
      return dateTime;
    },
    checkDayNumberWithWeekDay() {
      const dayIndex = new Date().getDay();
      const getDayName = (dayIndex: number) => {
        //TODO: key-value pairs če bomo imeli večjezičnost :)
        const days = ["ned", "pon", "tor", "sre", "čet", "pet", "sob"];
        return days[dayIndex];
      };
      const dayName = getDayName(dayIndex);
      return dayName;
    },
  },
});
